body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
html {
  -ms-overflow-style:none;
  overflow: -moz-scrollbars-none;
  scrollbar-width:none;
}

::-webkit-scrollbar { 
  display: none; 
}

html a {
  color: black
}

.slideButton {
  background-color: rgba(0,0,0,0);
  border: 0;
}
#prevButton:hover{
  background-image: linear-gradient(to right, rgba(255,255,255,0.4),rgba(0,0,0,0));
}
#nextButton:hover {
  background-image: linear-gradient(to left, rgba(255,255,255,0.4),rgba(0,0,0,0));
}
.form:hover {
  background-color: rgba(255,255,255,0.2);
  -webkit-text-decoration-color: white;
          text-decoration-color: white;
}
.item {
  -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
       animation: fadein 2s;
}

@media (orientation:portrait) {
  .col-3 {
    flex: 0 0 100%;
    max-width: 100%;
  }
}
@media (orientation:landscape) and (max-width: 767px) {
  #formSmall {
    display: flex !important;
  }
  #formInput {
    flex: 0 0 66.666667%;
    max-width: 66.666667%
  }
  #formHeader {
    flex: 0 0 33.333333%;
    max-width: 33.333333%
  }
}

.nav-link:hover {
  background-color: 'rgba(255, 255, 255, 0.3)';
  transition: 'background-color 2s ease';
}
